import {isEmail, isLength, isAlphanumeric} from 'validator';
import {findUser} from "../../_actions/userAction";

export const RegisterInfoValidator = (info, dispatch) => {

    const {username, email, password, passwordConfirm} = info;

    //  Backend   
    dispatch({
        ...findUser(),
        data: {
            username,
            email
        }
    });

    //  Frontend   
    let message = {
        warning: [],
        result: true,
    }

    if (!isAlphanumeric(username) || !isLength(username, { min:4, max: 16})) {
        if(username)
            message.warning.push(' 4~15      .');
        message.result = false;
    }

    if (!isEmail(email)) {
        if(email)
            message.warning.push("  ");
        message.result = false;
    }

    if(!isLength(password, { min: 5 })) {
        if (password)
            message.warning.push(' 6  .');
        message.result = false;
    }

    if (password !== passwordConfirm) {
        if (passwordConfirm)
            message.warning.push("   .");
        message.result = false;
    }

    return message;
}